<template>
    <div id="view-employee">
        <h3 id="matchNum"> Trận Đấu: {{match_id}} </h3>
        <div class="row ">
            <div class="col s2">
                <!-- Promo Content 1 goes here -->
                
            </div>
       
            <div class="col s4 m4 teamItem">
                <label id="teamName">{{home_team}}</label>
                <img class="card-image imgFlag" v-bind:src="home_flag" />
            </div>
            <div class="col s4 m4 teamItem">
                <label id="teamName">{{away_team}}</label>
                <img class="card-image imgFlag" v-bind:src="away_flag" />
            </div>
        </div>
        <div class="row teamItem">
            <p v-if="match_status == 'end'"><label id="teamName"> Tỉ Số Chung Cuộc: {{home_score}} - {{away_score}} </label></p>
            <p><label id="teamName"> Ngày Thi Đấu: {{start_time}} - {{start_date}}</label></p>
            <p id="teamName">Tình Trạng: 
                <label v-if="match_status == 'end'" id="teamName">KẾT THÚC</label>
                <label v-if="match_status == 'not_start'" id="teamName">CHƯA DIỄN RA</label>
                <label v-if="match_status == 'playing'" id="teamName">ĐANG THI ĐẤU</label>
            </p>
        </div>
        <div class="row teamItem">
            <p id="teamName"> TỈ LỆ KÈO 
                <router-link v-if="isLoggedIn" v-bind:to="{name: 'new-ratio', params: {match_id: match_id}}">
                    <a class="btn waves-effect waves-light red">THÊM KÈO</a>
                </router-link>
            <div class="col s12 m6 teamItem">
                <p id="teamName">Tỷ Số </p>
                <div class="col s4 m4">
                    <table class="tblCuoc">
                        <tr>
                            <th class="thTySo">Tỷ Số</th>
                            <th class="thTySo">Tỷ Lệ Cược</th>
                        </tr>
                        <tr v-for="sHome in listScoreHome" v-bind:key="sHome.id">
                            <td class="tdTySo">{{sHome.score}}</td>
                            <td class="tdTySo">{{sHome.ratio}}</td>
                        </tr>
                    </table>
                </div>
                <div class="col s4 m4">
                    <table class="tblCuoc">
                        <tr>
                            <th class="thTySo">Tỷ Số</th>
                            <th class="thTySo">Tỷ Lệ Cược</th>
                        </tr>
                        <tr v-for="sDraw in listScoreDraw" v-bind:key="sDraw.id">
                            <td class="tdTySo">{{sDraw.score}}</td>
                            <td class="tdTySo">{{sDraw.ratio}}</td>
                        </tr>
                    </table>
                </div>
                <div class="col s4 m4">
                    <table class="tblCuoc">
                        <tr>
                            <th class="thTySo">Tỷ Số</th>
                            <th class="thTySo">Tỷ Lệ Cược</th>
                        </tr>
                        <tr v-for="sDraw in listScoreAway" v-bind:key="sDraw.id">
                            <td class="tdTySo">{{sDraw.score}}</td>
                            <td class="tdTySo">{{sDraw.ratio}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="col s12 m6 teamItem">
                <p id="teamName">TRẬN - 90 Phút</p>
                <div class="col s12 m12 teamItem">
                    <table border="1">
                        <tr>
                            <th class="thTySo">Giá Chấp</th>
                            <th class="thTySo">Tỷ Lệ Cược</th>
                        </tr>
                        <tr v-if="chap != null">
                            <td class="tdTySo">{{chap.team}} chấp {{chap.num}} quả</td>
                            <td class="tdTySo">1 ăn {{chap.win}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
import db from './firebaseInit';
import firebase from 'firebase';

export default {
    name: 'view-employee',
    data () {
        return {
            match_id: null,
            match_status: null,
            home_team: null,
            away_team: null,
            home_score: null,
            away_score: null,
            start_time: null,
            start_date: null,
            home_flag: null,
            away_flag: null,
            listScoreHome: [],
            listScoreAway: [],
            listScoreDraw: [],
            chap: null,
            type: null,
            ratio: null,
            score: null,
            isLoggedIn: false,
            currentUser: false,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('matchs').where('match_id', '==', to.params.match_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.match_id = doc.data().match_id,
                    vm.match_status = doc.data().match_status,
                    vm.home_team = doc.data().home_team,
                    vm.away_team = doc.data().away_team,
                    vm.home_score = doc.data().home_score,
                    vm.away_score = doc.data().away_score,
                    vm.home_flag = doc.data().home_flag,
                    vm.away_flag = doc.data().away_flag,
                    vm.start_time = doc.data().start_time,
                    vm.start_date = doc.data().start_date
                });
            });
        });
        // db.collection('matchs').where('match_id', '==', to.params.match_id).collection('keotyso').get()
        // .then(querySnapshot => {
        //     querySnapshot.forEach(doc => {
        //         console.log(doc.data());
        //     });
        // });
    },
    watch: {
        '$route':'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('matchs').where('match_id', '==', this.$route.params.match_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    console.log(doc.data());
                    this.match_id = doc.data().match_id,
                    this.match_status = doc.data().match_status,
                    this.home_team = doc.data().home_team,
                    this.away_team = doc.data().away_team,
                    this.home_score = doc.data().home_score,
                    this.away_score = doc.data().away_score,
                    this.home_flag = doc.data().home_flag,
                    this.away_flag = doc.data().away_flag,
                    this.start_time = doc.data().start_time,
                    this.start_date = doc.data().start_date
                });
            });
        }
    },
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email;
        }        

        db.collection('keotyso').where('match_id', '==', this.$route.params.match_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    
                    console.log(doc.data());
                    if (doc.data().type != "chap") {
                        const data = {
                            'match_id': doc.data().match_id,
                            'type': doc.data().type,
                            'score': doc.data().score,
                            'ratio': doc.data().ratio
                        };

                        if (doc.data().type == 'home') {
                            this.listScoreHome.unshift(data);
                        } else if (doc.data().type == 'away') {
                            this.listScoreAway.unshift(data);
                        } else {
                            this.listScoreDraw.unshift(data);
                        }
                    }
                    else {
                        const data = {
                            'match_id': doc.data().match_id,
                            'type': doc.data().type,
                            'num': doc.data().num,
                            'team': doc.data().team,
                            'win': doc.data().win
                        }

                        this.chap = data;
                    }
                })
            })        
    }
}
</script>

<style>
    #matchNum {
        color: white
    }
    #teamName {
        color: white;
        font-size: 30px;
    }
    .teamItem {
        text-align:center;
        align-items: center;
    }
    table, tr, th, td {
        border: 2px;
        border-color: #fff;
        border-style: double;
    }
    .thTySo {
        padding: 3px;
        color: white;
        text-align: center;
    }

    .tdTySo {
        vertical-align: middle;
        text-align: center;
        color: white;
    }
</style>



