<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel green white-text center">
                        <h3>Thêm Kèo</h3>
                        <form>
                            <div class="row">
                                <div class="input-field">
                                    <p>
                                        <label>
                                            <input type="radio" id="chk_tiso" v-on:click="handleClick('keotiso')" checked />
                                            <span class="white-text">Kèo Tỉ Số</span>
                                        </label>

                                        <label>
                                            <input type="radio" v-on:click="handleClick('keotran')" id="chk_tran" />
                                            <span class="white-text">Kèo Trận</span>
                                        </label>
                                        </p>
                                </div>
                            </div>
                            <div id="keotiso">
                                <div class="row">
                                    <div class="input-field">
                                        <p>
                                            <label>
                                                <input type="radio" id="chk_tiso" v-on:click="handleClickTiSo('home')" />
                                                <span class="white-text">Đội Nhà</span>
                                            </label>

                                            <label>
                                                <input type="radio" v-on:click="handleClickTiSo('draw')" />
                                                <span class="white-text">Hoà</span>
                                            </label>
                                            <label>
                                                <input name="group2" type="radio" v-on:click="handleClickTiSo('away')"/>
                                                <span class="white-text">Đội Khách</span>
                                            </label>
                                            </p>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="input-field">                                
                                        <input type="text" id="score" v-model="score">
                                        <label class="white-text" for="score">Tỉ Số</label>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="input-field">                                
                                        <input type="text" id="ratio" v-model="ratio">
                                        <label class="white-text" for="ratio">Tỉ Lệ</label>
                                    </div> 
                                </div>
                            </div>
                            <div id="keotran">
                                <div class="row">
                                    <div class="input-field">                                
                                        <input type="text" id="ratio" v-model="team">
                                        <label class="white-text" for="ratio">Đội chấp</label>
                                    </div> 
                                </div>
                                <div class="row">
                                    <div class="input-field">                                
                                        <input type="text" id="ratio" v-model="num">
                                        <label class="white-text" for="ratio">Tỉ lệ</label>
                                    </div> 
                                </div>
                                <div class="row">
                                    <div class="input-field">                                
                                        <input type="text" id="ratio" v-model="win">
                                        <label class="white-text" for="ratio">Ăn</label>
                                    </div> 
                                </div>
                            </div>
                            <button v-on:click="saveKeo" class="btn btn-large red lighten-4 black-text">Tạo</button>
                            <router-link to="/" class="btn grew btn-large">Cancel</router-link>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseInit'

export default {
    name: 'ratio',
    data: function() {
        return {
            keoType: 'keotiso',
            match_id: null,
            type: null,
            score: null,
            ratio: null,
            team: null,
            num: null,
            win: null
        };
    },
    methods: {
        handleClick: function(value) {
            this.keoType = value;
            if(value == 'keotiso') {
                $('#keotran').hide();
                $('#keotiso').show();
            } else if (value =='keotran') {
                $('#keotran').show();
                $('#keotiso').hide();
            }
            //console.log(value);
            //alert('New value: ' + e.target.value);
        },

        handleClickTiSo: function(value) {
            this.type = value;
        },

        saveKeo: function(e) {            
            if (this.keoType == 'keotiso') {
                db.collection('keotyso').add({
                    match_id: this.$route.params.match_id,
                    type: this.type,
                    score: this.score,
                    ratio: this.ratio
                }).then(docRef => {
                    console.log(docRef);
                    this.$router.push('/');
                }).catch(err => {
                    console.log(err)
                })
            } else if (this.keoType == 'keotran') {
                db.collection('keotyso').add({
                    match_id: this.$route.params.match_id,
                    type: 'chap',
                    team: this.team,
                    num: this.num,
                    win: this.win
                }).then(docRef => {
                    console.log(docRef);
                    this.$router.push('/');
                }).catch(err => {
                    console.log(err)
                })
            }
            
        }
    },
    mounted: function() {
       $(document).ready(function(){            
            $('#keotran').hide();
            $('#keotiso').show();
        });

    }
}
</script>



