<template>
    <div id="dashboard">
          <div class="row">
            <div v-for="match in matches" v-bind:key="match.id" class="col s12 m4">
                <div class="card-panel green darken-2">
                    <span class="matchNumber">Trận: {{match.match_id}}</span>
                    <div class="col s12 m12 white-text">
                        <div class="col s12 m6 teamItem">
                            <label class="teamName">{{match.home_team}}</label>
                            <img class="card-image imgFlag" v-bind:src="match.home_flag" />
                            <label v-if="match.match_status == 'end'" class="score">{{match.home_score}}</label>
                        </div>
                        <div class="col s12 m6 teamItem">
                            <label class="teamName">{{match.away_team}}</label>
                            <img class="card-image imgFlag" v-bind:src="match.away_flag" />
                            <label v-if="match.match_status == 'end'" class="score">{{match.away_score}}</label>
                        </div>
                    </div>

                    <div class="card-action teamItem">
                        <p class="infoText">Thời gian: {{match.start_time}} - {{match.start_date}}</p>
                        <p>
                            <a v-if="match.match_status == 'end'" class="btn waves-effect waves-light blue-grey darken-1">KẾT THÚC</a>
                            <a v-if="match.match_status == 'not_start'" class="btn waves-effect glight-blue darken-4">CHƯA BẮT ĐẦU</a>
                            <a v-if="match.match_status == 'playing'" class="btn waves-effect waves-light red darken-1">ĐANG THI ĐẤU</a>
                        </p>
                        <p>
                            <a v-if="match.match_status == 'end'" class="btn waves-effect waves-light purple darken-1">Chi Tiết</a> 
                            <router-link v-if="match.match_status != 'end'" v-bind:to="{name: 'view-match', params: {match_id: match.match_id}}">
                                <a class="btn waves-effect waves-light pink darken-1">SOI KÈO</a>
                            </router-link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
           

        <!-- <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <div class="chip">{{employee.dept}}</div>
                {{employee.employee_id}}:{{employee.name}}
            </li>
        </ul> -->
        <!-- <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div> -->
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
    data () {
        return {
            employees: [],
            matches: [],
        }
    },
    created () {
        db.collection('matchs').orderBy('match_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'match_id': doc.data().match_id,
                    'match_status': doc.data().match_status,
                    'start_time': doc.data().start_time,
                    'start_date': doc.data().start_date,
                    'home_team': doc.data().home_team,
                    'away_team': doc.data().away_team,
                    'home_score': doc.data().home_score,
                    'away_score': doc.data().away_score,
                    'home_flag': doc.data().home_flag,
                    'away_flag': doc.data().away_flag
                }

                this.matches.push(data);
            });
        });
    }
}
</script>

<style>
.imgFlag {
    width: 100%;
}
.teamItem {
    text-align:center;
    align-items: center;
}
.teamName {
    font-size: 25px;
    color: white;
}
.score {
    font-size: 40px;
    color: white;
}
.infoText {
    color: white;
    font-size: 15;
    font-weight: 400;
}
.matchNumber {
    color: #80cbc4;
    font-size: 40px;
    font-weight: 500;
}
</style>




